@charset "utf-8";
body{
 
letter-spacing: -0.5px;
/* width: 414px;
margin: 0px auto; */
}.end{
    display: grid;
    grid-template: repeat(2,1fr) / repeat(2,1fr);
    justify-items: center;
    margin: 0 5vw;
  }
  .end div{
    padding: 10px;
    width: 100%;
    display: block;
    border: 1px solid #7B7D8D;
    text-align: center;
    border-left: none;
    border-right: none;
    box-sizing: border-box;
  }
  .end .service, .end .trade{
    border-right: 1px solid #7B7D8D;
  }
  .end .service, .end .map{
    border-bottom: none;
  }
  .end div a{
    color: #7B7D8D;
    text-decoration: none;
  }
  .end_text{
    margin: 20px 5vw;
    font-size: 14px;
    color: #7B7D8D;
  }
  .gopofo{
    font-size: 24px; font-weight: bold;
    color: #7B7D8D;
     text-align: center;
     padding: 50px 0;
     position: relative;
     transition: 1s;
  }

  span{
    color: #ff7ab1;
  }
  .gopofo:hover{
    animation: ani_1 1s infinite alternate;
  }
  @keyframes ani_1 {
    0%{
      transform: translateY(0px);
    }
    100%{
      transform: translateY(-10px);
    }
  }