@charset "utf-8";
.swiper {
    width: 100%;
    height: 537px;
    margin-top: 50px;
  }
  
  .swiper-slide {
    text-align: center;
    position: relative;
    font-size: 18px;
    background: rgb(59, 59, 59);
    border-radius: 0 0 5% 5%;
    /* Center slide text vertically */
    display: -webkit-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    -webkit-justify-content: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    -webkit-align-items: center;
    align-items: center;
  }
  
  .swiper .swiper-wrapper #img1{
    width: 100%;
    height: 537px;
    overflow: hidden;
  }
  .swiper .swiper-wrapper #img2{
    width: 100%;
    height: 537px;
    overflow: hidden;
  }
  .swiper .swiper-wrapper #img3{
    width: 100%;
    height: 537px;
    overflow: hidden;
  }
  .swiper .swiper-wrapper #img1 > img{
    object-fit: cover;
    width: 100%;
    height: 100%;
  }
  .swiper .swiper-wrapper #img2 > img{
    object-fit: cover;
    width: 100%;
    height: 100%;
  }
  .swiper .swiper-wrapper #img3 > img{
    object-fit: cover;
    width: 100%;
    height: 100%;
  }
  .blackbox{
    position: absolute;
    color: white;
    background: rgb(0,0,0);
    background: linear-gradient(180deg, rgba(0,0,0,0) 0%, rgba(0,0,0,0.62) 100%);
    width: 100%;
    height: 537px;
    border-radius: 0 0 5% 5%;
    display: grid;
    place-content: center;  
  }
  
  .blackbox h2{font-weight: bold; font-size: 32px;}
  .blackbox h3{font-weight: bold; font-size: 28px;margin-top: 1vh; margin-bottom: 3vh;}
  .blackbox p{color: #bbbbbb; margin-bottom: 3vh;}
  .blackbox .view{}
  .view a{display: block;
  width: 130px; height: 40px; text-align: center; margin: 0px auto;
  color: white; line-height: 40px;
  border-radius: 25px;
  /* background: rgb(255,116,181); */
  background: #ededed;}
  
  .swiper-horizontal>.swiper-pagination-bullets, .swiper-pagination-bullets.swiper-pagination-horizontal, .swiper-pagination-custom, .swiper-pagination-fraction{
    bottom: 40px;
  }