body{
  
letter-spacing: -0.5px;
/* width: 414px;
margin: 0px auto; */
}
.AD{margin: 60px 5vw;  color: white; font-family: 'SBAggroB';position: relative;}
.AD .ADbox{
  margin-top: 10vh;
  background-color: black;
  border-radius: 16px;
  padding: 15px 20px;
  display: flex;
  flex-direction: column;
  margin-bottom: 20vh;
  
}
.AD .ADbox h4{font-family: 'OTEnjoystoriesBA',sans-serif; font-size: 24px; }
.AD .ADbox h4::after{content: "";width: 80px;height: 1px; display: inline-block; background-color: #fff;margin-left:1vw;margin-bottom: 1vh;}
.AD .ADbox .middle{display: flex;}
.AD .ADbox h1{font-size: 32px; color: #FF7AB1; font-weight: bold; margin-right: 2vw;}
.AD .ADbox h2{font-size: 16px; }
.AD .ADbox h3{margin-top: 5px; font-weight: 400;}
.AD .ADbox .cellphone{position: absolute; width: 200px; height: 17vh; right: 0; top: 28px;}
