}
.view a{display: block;
    width: 130px; height: 40px; text-align: center; margin: 0px auto;
    color: white; line-height: 40px;
    border-radius: 25px;
    background: rgb(255,116,181);
    background: linear-gradient(90deg, rgba(255,116,181,1) 0%, rgba(255,152,152,1) 100%);}

    .country{
        margin: 60px 5vw; text-align: center;
      }
      .country .grid{width: 100%;  position: relative; display: grid;
      grid-template: repeat(2,1fr)/repeat(2,1fr); gap: 10px;}
      .country .grid .box1{position: relative;
        width: 100%; height: 203px;border-radius:12px;}
      .con_text{color: white; position: absolute; bottom: 20px; text-align: left; margin-left: 3vw;}
      .con_text .con_word{font-weight: bold; font-size: 20px;}
      .con_text .con_subword{margin: 10px 0px;}
      .con_text .con_subword_2{font-size: 18px;}
      .grid img{
       border-radius: 16px;
        width: 100%;
       height: 100%;
        object-fit: cover;
      }
      .country .grid .box2{position: relative;
        width: 100%; height: 203px;border-radius:12px;}
      .country .grid .box3{position: relative;
        width: 100%; height: 203px;border-radius:12px;}
      .country .grid .box4{position: relative;
        width: 100%; height: 203px;border-radius:12px;}
 
      .country > .view{
        margin-top: 40px;
      }
      .view a{display: block;
        width: 130px; height: 40px; text-align: center; margin: 0px auto;
        color: white; line-height: 40px;
        border-radius: 25px;
        background: rgb(255,116,181);
        background: linear-gradient(90deg, rgba(255,116,181,1) 0%, rgba(255,152,152,1) 100%);}
      
.selectbox{font-size: 18px; margin-bottom: 30px; width: 100%;}
.selectbox ul{display: flex;align-items: center;  margin: 0px auto;flex-shrink: 0; width: 100%; flex-wrap: wrap; justify-content: center;}
.selectbox li{width: 20vw; border: 1px solid #ededed; border-radius: 40px; margin-right: 1vw;margin-bottom: 10px;}
.selectbox li a{ color: #999; display: block; padding: 10px;  } 
.selectbox li a.active{
  background-color: #ededed; color: white; border-radius:30px ;
  overflow: hidden;
}
.blackbox_3{
  position: absolute;
  background: linear-gradient(180deg, rgba(0,0,0,0) 0%, rgba(0,0,0,0.4) 100%);
  width: 100%;
  height: 100%;
  border-radius: 0 0 5% 5%;
  top: 0;
}