@charset "utf-8";
body{
letter-spacing: -0.5px;
/* width: 414px;
margin: 0px auto; */
}

.title{
    font-size: 28px; font-weight: bold;
    margin-bottom: 14px;
  }
  .subtitle{
    color: #7B7D8D; font-size: 16px;margin-bottom: 40px;
  }
.view a{display: block;
    width: 130px; height: 40px; text-align: center; margin: 0px auto;
    color: #999; line-height: 40px;
    border-radius: 25px;
    background: rgb(255,116,181);
    background: #ededed; text-decoration: none;}

    .deal    {
  margin: 60px 3vw; text-align: center;
}
.deal .dealbox{
  width: 100%;  background-color: #fff;
  border-radius: 16px;
  margin-bottom: 20px;
  -webkit-box-shadow: 0px 10px 15px -4px rgba(0,0,0,0.2); 
box-shadow: 0px 10px 15px -4px rgba(0,0,0,0.2);
}
.deal .deal_img{
  width: 100%; 
  height: 220px;
  border-radius: 16px 16px 0 0;
  overflow: hidden;
  
  text-align: center;
}
.deal .deal_img img{
  width: 100%;
  height: 100%;object-fit: cover;
  object-position: 50%;
  display: block;
}
.textbox {text-align: left; padding: 0px 20px;}
.textbox h4{margin-top: 16px;}
.textbox h2{font-weight: bold; font-size: 20px; margin: 5px 0;}
.textbox .price{
  text-align: right;
  text-decoration: line-through;
  color: #7B7D8D;
  margin-top: 5px;
}
.textbox .price_main{
  display: flex;
  justify-content: space-between;
}
.textbox .price_main .num{color: #7B7D8D; line-height: 33px;}
.textbox .price_main .price_deal{font-size: 22px; font-weight: bold; margin-bottom: 14px;}
.textbox .price_main .price_deal span{font-size: 20px; font-weight: bold; color: #FF7AB1;}

.deal > .view{
  margin-top: 40px;
}