@charset "utf-8";

.view a{display: block;
    width: 130px; height: 40px; text-align: center; margin: 0px auto;
    color: white; line-height: 40px;
    border-radius: 25px;
    background: rgb(255,116,181);
    background: linear-gradient(90deg, rgba(255,116,181,1) 0%, rgba(255,152,152,1) 100%); text-decoration: none;}
    .review{
        text-align: center;
       background-color: black;
       padding: 70px 0px;
     }
     .review .title_white{color: #fff;font-size: 28px; font-weight: bold;
       margin-bottom: 14px; }
     .review .subtitle{}
     .review .reviewbox{ padding-bottom: 30px; margin: 0px 6vw; border-radius: 16px;background-color: #fff; }
     .review .reviewbox .review_img{position: relative;
       height: 300px;border-radius: 16px 16px 0 0 ;}
       .reviewbox .review_img img{
        width: 100%;
        height: 100%;
        object-fit: cover;
        border-radius: 16px 16px 0 0 ;
       }
     .reviewbox header{display: flex;  align-items: center; margin: 20px 0;}
     .icon{
        width: 30px; height: 30px;
       
       margin-left: 5vw;
     }
     .reviewbox header h4{text-align: left; font-size: 18px;margin-bottom: 8px;}
     .reviewbox header h5{color: #797979; text-align: left;}
     .reviewbox header .insta{color: #797979; flex-grow: 1; margin-right: 2vw;}
     .reviewbox p{
       padding: 0px 19px; text-align: left; line-height: 1.4;
     }
     .mid{
       width: 57%;
       margin-left: 3vw;
     }
     .review > p{
       
     }
     .review > p a{
       margin-top: 40px;color: #999; display: block;
       text-decoration: none;
       margin: 40px auto;
       position: relative;
     }
     
     .review > p a img{
      
       background: url(../assets/images/arrow.svg) no-repeat 50% /cover;
       width:24px; height:24px; position: absolute; top: -5px;
     }