@charset "utf-8";

.title{
    font-size: 28px; font-weight: bold;
    margin-bottom: 14px;
  }
  .subtitle{
    color: #7B7D8D; font-size: 16px;margin-bottom: 40px;
  }
  .blackbox_2{
    position: relative;
    background: linear-gradient(180deg, rgba(0,0,0,0) 0%, rgba(0,0,0,0.4) 100%);
    width: 100%;
    height: 100%;
    border-radius: 0 0 5% 5%;
    z-index: 99;
  }
.country{
    margin: 120px 5vw; text-align: center;
  }
  .country .grid2{width: 100%; position: relative; display:grid;
  gap: 10px; grid-template: none;}
 .first ,.second ,.third ,.four{
  position: relative;
  width: 100%; height: 100%;border-radius:12px;
      z-index: 9;transition: 0.5s;
 }
 

  .country .grid2 .box1{
    width: 100%; height: 203px;border-radius:12px; position: relative;}
    .gridimg1{
      /* background: url(../assets/images/gal1.jpg) no-repeat 50% / cover; */
      width: 100%; height: 100%;border-radius:12px;
      z-index: 9;transition: 0.5s;
      position: relative;
    }
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
      border-radius:12px;
    }
  .con_text2{color: white; position: absolute; bottom: 20px; text-align: left; margin-left: 20px;z-index: 99;}
  .con_text2 .con_word{font-weight: bold; font-size: 20px;}
  .con_text2 .con_subword{margin: 10px 0px;}
  .con_text2 .con_subword_2{font-size: 18px;}
  
  .country .grid2 .box2{
    width: 100%; height: 203px;border-radius:12px; position: relative; margin: 10px 0;}
