@charset "utf-8";
body{
  
letter-spacing: -0.5px;
/* width: 414px;
margin: 0px auto; */
}

.cos{margin: 60px 5vw; text-align: center;}
.title{
  font-size: 28px; font-weight: bold;
  margin-bottom: 14px;
}
.subtitle{
  color: #7B7D8D; font-size: 16px;margin-bottom: 40px;
}
.cos .imgbox{}
.cos .imgbox p{font-size: 20px; font-weight: bold; text-align: left; margin-left: 30px;margin-bottom: 10px;}
.cos .imgbox p span{color: #FF7BAF; }
.cos .imgbox .imgbox_1{
  /* background: url(../assets/images/cos1.jpg)no-repeat 50% /cover ; */
  width: 100%; 
  height: 260px; border-radius: 16px;
  overflow: hidden;
  position: relative;
}
.cos .imgbox .imgbox_1 img{
  
  object-fit: cover;
  width: 100%; 
  height: 100%;
}
.cos .imgbox .imgbox_1 .text{ position: absolute; bottom: 24px; color: white; text-align: left; margin-left: 20px;font-size: 20px;}
.cos .imgbox .imgbox_1 .word{ font-weight: bold; margin-bottom: 10px;}
.blackbox_3{
  position: absolute;
  background: linear-gradient(180deg, rgba(0,0,0,0) 0%, rgba(0,0,0,0.4) 100%);
  width: 100%;
  height: 100%;
  border-radius: 0 0 5% 5%;
  top: 0;
}
.cos > .view{
  margin-top: 40px;
}
.view a{display: block;
    width: 130px; height: 40px; text-align: center; margin: 0px auto;
    color: white; line-height: 40px;
    border-radius: 25px;
    background: rgb(255,116,181);
    background: linear-gradient(90deg, rgba(255,116,181,1) 0%, rgba(255,152,152,1) 100%);}